<template>
  <h1>Справка по использованию</h1>
  <div v-html="content"></div>
</template>

<script>
import { useMeta } from "vue-meta"
import { get, call } from 'vuex-pathify'

export default {
  name: "Help",
  data() {
    return {
    }
  },
  computed: {
    content: () => get('help@help').value,
  },
  mounted() {
    this.loadHelp()
  },
  methods: {
    ...call('help', [
      'loadHelp',
    ]),
  },
  setup() {
    useMeta({
      title: "Справка | Телефонный справочник [версия 3.0]",
    });
  },
}
</script>